@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300;400;600;700;900&display=swap);
.for-container{border:0px solid transparent !important}.for-container .for-toolbar{background-color:#1c1c1e;font-weight:700;border-radius:0px !important;border-bottom:1px solid #cccccc}.for-container .for-toolbar ul li{color:#cccccc}.for-container .for-toolbar ul li .for-subfield,.for-container .for-toolbar ul li .for-eye-off{font-weight:500;color:#1c1c1e}.for-container .for-editor-block{background-color:#1e2126;color:#ffffff}.for-container .for-editor-block .for-line-num{color:#eda35e;font-weight:600;background-color:#1e2126 !important;border-right:1px solid #cccccc}.for-container .for-editor-block .for-editor-content textarea{font-family:"Source Code Pro", monospace;font-weight:700;caret-color:#ff6600}

*{box-sizing:border-box}html{width:100%;height:100%}body{position:relative;width:100%;height:100%;margin:0;font-family:'Source Sans Pro', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

